.industriesWrapper{
  min-height: 600px;
  height: 100%;
  width: 100%;
  .industriesWrapperDark{
    align-items: center;
    padding-top: 5.5%;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    //opacity: 0.5;
    content: "";
    display: flex;
    justify-content: center;
     .wrapper{
       display: flex;
       flex-direction: row;
       gap:20%;
       width: 80%;
       .info{
         top:0;
         display: flex;
         flex-direction: column;
         color: white;
         width: 40%;
         .h1{
           overflow-wrap: break-word;
           hyphens: auto;
           margin: 0;
           display: block;
           font-size: 2em;
           margin-block-start: 0.67em;
           margin-block-end: 0.67em;
           margin-inline-start: 0;
           margin-inline-end: 0;
           font-weight: bold;
           unicode-bidi: isolate;

         }
         p{
           padding-top: 2%;
           font-size: 17px;
           font-weight: 600;
           line-height: 27px;
           margin-top: 0;
           margin-bottom: 1rem;
         }
         .address{
           p{
             font-size: 17px;
             font-weight: 600;
             line-height: 27px;
             margin-top: 0;
             margin-bottom: 1rem;
           }
         }
         .allRights{
           margin-top: 67px;
         }
       }
       .form{
         width: 40%;
       }
     }
}
}
